let defaultConfigs = {
	defaultOptions: {
		mode: 'text/html',
		theme: 'material',
		lineNumbers: true,
		tabSize: 2
	},
	optionsjs: {
		mode: 'javascript',
		theme: 'material',
		lineNumbers: true,
		tabSize: 2
	},
	headerBg: '#ffffff'
}

export default function configs(state = defaultConfigs, action = {}) {
	if (action.type == 'headerBg') return { ...state, ...{ headerBg: action.value } }
	return state
}